import ColumnTextImage from '../../components/ColumnTextImage';
import { QuoteSection } from '../../components/quote-section';
import { Wrapper } from '../styles';
import { ContainerSection, ImgContainer } from './styles';

const Home = () => {
	return (
		<>
			<ImgContainer
				src='/acquamater/home-intro.png'
				alt='Acqua Mater introduo'
			/>
			<Wrapper>
				<ColumnTextImage
					image={{
						img: '/acquamater/acqua-mater-name.png',
						isBg: false,
					}}
					buttons={[
						{ title: 'Saiba Mais', url: '/quem-somos' },
						{
							title: 'Assista ao filme SOMOS GUA',
							url: 'https://www.youtube.com/watch?v=W740g2Shb7s',
						},
					]}
				>
					Somos uma organizao brasileira com atuao global que
					incentiva a gerao de mais sade e mais vida a partir do
					cuidado de todas as guas que circulam no planeta. Nossos
					projetos transdisciplinares so voltados  Conservao, 
					Regenerao e  Sustentabilidade de toda a vida que habita o
					Mundo GUA-OCEANO
				</ColumnTextImage>
				<ColumnTextImage
					title='Patrcia Furtado de Mendona'
					image={{
						img: '/acquamater/patricia-furtado.png',
						isBg: false,
					}}
					buttons={[
						{
							title: 'Saiba Mais',
							url: '/patricia-furtado',
						},
					]}
				>
					- Brasileira e Italiana, Empreendedora, Palestrante
					Inspiracional/Storyteller, Defensora do Oceano e das guas,
					Facilitadora de Experincias de Reconexo com a gua e com o
					Oceano, Mergulhadora; <br />
					- "Profissional de ImPacto" da Rede Brasil do Pacto Global
					da ONU; <br />
					- Co-lider do Componente "Treinamento" do OLWA (Ocean
					Literacy With All), programa coordenado pela COI-UNESCO e
					endossado pela Dcada do Oceano da ONU; <br />
					- Coordenadora-Brasil da RELATO (Red de Educacin
					LatinoAmericana para el Ocano); <br />- Mentora e
					Conselheira de vrias organizaes internacionais voltada
					para jovens, como Waterlution, Sustainable Ocean Alliance,
					UNFCCC YOUNGO Ocean's Voice, entre outras.
				</ColumnTextImage>
			</Wrapper>
			<QuoteSection
				background='/acquamater/bg-prada-quote.png'
				text='"Estamos extremamente orgulhosos de continuar tendo Patricia Furtado de Mendona desempenhando um papel ativo no projeto SEA BEYOND, o programa educacional desenvolvido pelo Grupo Prada em colaborao com a COI-UNESCO, para difundir os princpios da Cultura Ocenica em todo o mundo."'
				author='EQUIPE DE COMUNICAO CORPORATIVA DO GRUPO PRADA/ITLIA'
				authorLogo='/acquamater/prada-logo.png'
				textStyle='body1bold'
			/>

			<ColumnTextImage
				reverse
				invertedColors
				image={{
					img: '/acquamater/o-que-oferecemos-bg.png',
					isBg: true,
				}}
				title='O que Oferecemos'
				buttons={[{ title: 'Saiba Mais', url: '/o-que-oferecemos' }]}
				personalize
			>
				Todos os Produtos da Acqua Mater so realizados tendo por eixo a
				GUA e O OCEANO e tem sinergia entre si, podendo se articular
				dependendo das necessidades do cliente.
				<br />
				<img
					src='/acquamater/o-que-oferecemos-graph.png'
					alt='o-que-oferecemos grfico'
					style={{ marginTop: '15px', width: '500px' }}
				/>
			</ColumnTextImage>

			<ContainerSection>
				<ColumnTextImage
					reverse
					image={{
						img: '/acquamater/como-atuamos-bg.png',
						isBg: false,
					}}
					title='Como atuamos'
					buttons={[
						{
							title: 'Conhea nosso Advisory Board',
							url: '/quem-pensa-e-faz#advisory-board',
						},
						{
							title: 'Conhea nossos Parceiros',
							url: '/parceiros',
						},
					]}
				>
					Tudo comea com uma troca: ouvimos as necessidades de cada
					cliente e, a partir da, desenhamos e apresentamos a soluo
					que consideramos ser a mais assertiva. Os produtos so
					personalizados, nascem de um processo de construo
					colaborativa que tambm inclui a participao de nosso
					Advisory Board e de uma ampla rede de Parceiros.
				</ColumnTextImage>
			</ContainerSection>
		</>
	);
};

export default Home;
