import { Head } from '../../components/head';
import { Box, Grid, Typography } from '@mui/material';
import BoxGray from '../../components/BoxGray';
import { colors } from '../../theme/colors';
import { PersonQuoteCard } from '../../components/person-quote-card';
import { Wrapper } from '../styles';

export const NossaHistoria = () => {
	return (
		<Box>
			<Head
				breadcrumbs={[
					{ url: '/', name: 'Home' },
					{ url: '/acqua-mater', name: 'Acqua Mater' },
				]}
				title='Nossa Histria'
				backgroundImage='/acquamater/default-head-bg.png'
			>
				<Typography variant='body1' color='white'>
					A misso da Acqua Mater  ambiciosa, pois se prope a ser um
					agente de mudanas que oferece solues inovadoras e
					criativas para promover a sade e a vida sob as esferas
					pessoal, social e ambiental, sempre tendo as guas e o
					Oceano como ponto de partida, e tambm de chegada.
				</Typography>
			</Head>

			<Wrapper>
				<Grid
					container
					pt='100px'
					pb='20px'
					mx='auto'
					spacing={2}
					justifyContent='center'
					display='flex'
				>
					<Grid item md={6}>
						<Typography variant='body1'>
							Por isso, a Acqua Mater nasce de um propsito de
							vida que  tambm Misso: retribuir o que as guas e
							o Oceano sempre lhe proporcionaram. Para isso,
							convida pessoas e organizaes a se reconectarem com
							as origens da vida e a cuidarem de todas as guas,
							inspirando-se no velho ditado: "quem ama, cuida".
							<br /> <br />
							Os primeiros anos confirmaram que a deciso fora
							acertada, principalmente diante das urgncias
							socioambientais planetrias: escassez hdrica,
							poluio de rios e mares, crise climtica etc. Mas
							tambm diante da desconexo do ser humano da
							natureza, acentuada tanto pelas demandas do
							cotidiano urbano quanto da vida online, sobretudo
							aps a pandemia.
						</Typography>
					</Grid>
					<Grid item md={6}>
						<Typography variant='body1'>
							A misso da Acqua Mater  ambiciosa, pois se prope
							a ser um agente de mudanas que oferece solues
							inovadoras e criativas para promover sade e a vida
							sob as esferas pessoal, social e ambiental, sempre
							tendo as guas e o Oceano como ponto de partida, e
							tambm de chegada.
							<br /> <br />
							Como diz Eduardo Galeano, "a utopia nos faz
							avanar". O futuro no  consequncia s do passado,
							mas tambm do presente. E do que cada um de ns faz
							com o presente da vida nas mos.
						</Typography>
					</Grid>
				</Grid>

				<BoxGray waterMark dense center>
					<Typography variant='h2' color={colors.blueLighter}>
						O NOME
					</Typography>
					<Typography variant='body1bold' mt='15px'>
						Acqua Mater {`<>`} gua Me
					</Typography>
					<Typography variant='body1'>
						A escrita correta em latim seria Aqua Mater. A
						introduo da letra "c"  uma "licena potica",
						homenagem  escrita da palavra "gua" em italiano, pois
						foi na Itlia que sua fundadora, Patrcia Furtado,
						iniciou seus estudos sobre as Memrias da gua e as
						nossas Memrias Marinhas.
					</Typography>
				</BoxGray>

				<Box width='80%' marginX='auto' marginY='50px'>
					<img
						width='100%'
						src='/acquamater/other-acquamater-logos.png'
						alt='Acqua Mater logos'
					/>
				</Box>
				<Box display='flex' alignItems='center' justifyContent='center'>
					<PersonQuoteCard
						quote='"Os projetos da Acqua Mater produzem um resultado extremamente potente e raro por fazerem convergir o olhar humano, tcnico e artstico sobre a gua."'
						personImg='/acquamater/quem-pensa-faz/people/dawn-fleming.png'
						name='Dawn Fleming'
						extraContent={
							<Typography variant='body1'>
								Canad / Brasil
							</Typography>
						}
						description='Diretora do Water Innovation Lab Brasil / WIL BRASIL'
					/>
					<PersonQuoteCard
						quote='"Patrcia Furtado  uma inspirao e uma Empoderadora Mulher Global, porque suas aes inspiram outras pessoas a sonhar mais, aprender mais, fazer mais e se tornar mais. Ela  uma verdadeira lder!"'
						personImg='/acquamater/quem-pensa-faz/people/anais-barclay.png'
						name='Anais Barclay'
						extraContent={
							<Typography variant='body1'>
								Laos / Hong Kong
							</Typography>
						}
						description='Fundadora da BubblyStarzs & CEO da MEDRadiance Organization'
					/>
				</Box>
			</Wrapper>
		</Box>
	);
};
