import { Box, Typography } from '@mui/material';
import { Head } from '../../components/head';
import {
	GraphTooltip,
	InteractiveContainer,
	InteractiveGraph,
	GraphTooltipContainer,
} from './style';
import { colors } from '../../theme/colors';
import { QuoteSection } from '../../components/quote-section';
import ColumnTextImage from '../../components/ColumnTextImage';
import { Wrapper } from '../styles';

export const QuemSomos = () => (
	<Box overflow='hidden'>
		<Head
			breadcrumbs={[
				{ url: '/', name: 'Home' },
				{ url: '/acqua-mater', name: 'Acqua Mater' },
			]}
			title='Quem somos'
			backgroundImage='/acquamater/default-head-bg.png'
		>
			Somos uma organizao brasileira com atuao global que incentiva a
			gerao de mais sade e mais vida a partir do cuidado de todas as
			guas que circulam no planeta.
			<br />
			<br />
			Promovemos:
			<ul>
				<li>uma nova cultura da gua;</li>
				<li>a cultura ocenica;</li>
				<li>o dilogo entre o ODS 6 e o ODS 14;</li>
				<li>
					a reconexo de indivduos e coletivos com a gua e o Oceano
				</li>
			</ul>
		</Head>

		<Wrapper>
			<InteractiveContainer>
				<Typography
					variant='body1'
					color={colors.blueLighter}
					fontWeight='bold'
				>
					Nossos projetos transdisciplinares so voltados 
					Conservao,  Regenerao e  Sustentabilidade de todos os
					organismos que habitam o Mundo GUA-OCEANO.
				</Typography>

				<GraphTooltip
					title='Reconstituio de algo que foi destruido ou arruinado'
					mt='20px'
					divTitle='Regenerao'
				/>
				<InteractiveGraph
					src='/acquamater/quem-somos-graph.png'
					alt='Grfico'
				/>
				<GraphTooltipContainer>
					<GraphTooltip
						placement='top'
						title='Estado do organismo que est em equilbrio com o ambiente em que se encontra'
						divTitle='Conservao'
						bottomText
					/>
					<GraphTooltip
						placement='top'
						title='Capacidade de criar meios para suprir as necessidades bsicas do presente sem afetar as geraes futuras'
						divTitle='Sustentabilidade'
						bottomText
					/>
				</GraphTooltipContainer>
			</InteractiveContainer>
		</Wrapper>

		<QuoteSection
			text='Por que falamos em mundo GUA-OCEANO?'
			background='/acquamater/quem-somos-quote-bg.png'
			textStyle='h2lite'
		/>

		<ColumnTextImage
			reverse
			image={{
				isBg: true,
				img: '/acquamater/quem-somos-crianca-agua-bg.png',
			}}
			personalize
		>
			Todas as guas que circulam em nosso planeta esto conectadas. Isso
			acontece desde quando elas vieram do espao e deram origem ao oceano
			primordial. Por sua vez, dele saram as guas que hoje integram o
			ciclo natural hdrico, que continua promovendo o transporte das
			guas entre mar, cu e terra, sob suas variadas formas. Essas guas
			naturais tambm se conectam s guas que foram domesticadas pelo
			ser humano, por meio de variados servios de tratamento, transporte
			e saneamento que viabilizam a vida urbana e a produo dos inmeros
			bens materiais que consumimos.
			<br />
			<br />
			Podemos afirmar que, desde sempre,  a mesma gua que circula e
			permite a continuidade da vida na Terra, renovando-se, reciclando-se
			 inclusive, dentro de ns. Todos os ecossistemas aquticos, de gua
			doce e salgada, esto interligados entre si e tambm com os vrios
			fatores que regem o ciclo natural e o ciclo artificial das
			guas. Basta lembrar que da natureza tiramos as guas que usamos e
			para ela as devolvemos.
			<br />
			<br />
			<Typography color={colors.blueLighter}>
				Portanto, cada gota de gua em terra  filha do mar. De l ela
				vem. E para l, um dia, ela volta.
			</Typography>
			<br />
			No podemos nos esquecer de que aproximadamente 80% da poluio
			marinha tem sua origem em terra.  cuidando de todas as guas que
			podemos, tambm, cuidar da gua do mar.
			<br />
			<br />
		</ColumnTextImage>
		<img alt='gua' src='/acquamater/quem-somos-bottom.png' />
	</Box>
);
