import { Grid, Typography } from '@mui/material';
import ColumnTextImage from '../../../components/ColumnTextImage';
import {
	ContainerTextSingle,
	ContainerTitle,
	HeadPage,
	Title,
} from '../styles';
import {
	List,
	TitleThemesBox,
	TitleThemesBlue,
	ContainerThemes,
} from './styles';
import { Wrapper } from '../../styles';
import BoxGray from '../../../components/BoxGray';
import { Strip } from '../../../components/Strip';

const Talk = () => {
	const listAllThemesLeft = [
		'Cultura Ocenica como ferramenta para o sucesso da Dcada do Oceano da ONU',
		'Cultura Ocenica no Setor Privado: avanando com Indicadores ESG',
		'Sustentabilidade do Oceano & ODS 14: Desafios da Dcada do Oceano da ONU',
		'O dilogo entre o ODS 6 (gua e Saneamento) e o ODS 14 (Vida na gua)',
		'Plstico no Mar, no Ciclo Hdrico e Dentro de Ns: o que fazer?',
		'gua-Oceano ao centro dos demais ODS',
		'Liderana & Filosofia da Agua',
	];

	const listAllThemesRight = [
		'Oceano: um Paradigma da Colaborao e da Cooperao ',
		'Carreira: Propsito & Realizao',
		'Jovens construindo o presente, e no s futuro',
		'gua, Oceano & Mulheres',
		'Sade da gua / Sade do Oceano / Sade Humana',
		'Por que devemos nos (re)conectar ao Mundo gua-Oceano?',
		'A Sabedoria da gua: veculo de autoconhecimento, autocuidado e transformao',
		'Um Oceano de Memrias: guas que nos atravessam e habitam.',
	];

	const listMacrothemes = [
		'Regenerao, Sustentabilidade, Agenda 2030 da ONU',
		'Biodiversidade e Conservao de todos os Ecossistemas Aquticos',
		'Contaminao das guas por diversos poluentes e impactos na fauna e na flora',
		'Solues Baseadas na Natureza',
		'Novas Tecnologias & Novos Negcios de Impacto Socioambiental',
		'Fundos de Investimento / Indexes ESG ligados  gua e ao Oceano',
		'Economia Circular',
		'Relao com Sociedades e Culturas, com Gnero e Diversidade',
		'Projetos de Educao Ambiental focados nas guas',
		'Os vrios desafios ligados ao Nexo gua - Oceano',
		'Clima',
	];

	const listMacroThemesLeft = [
		'Gesto de Recursos Hdricos, Acesso  gua & - Saneamento no Brasil',
		'Escassez e Resilincia Hdrica',
		'Polticas Pblicas & Marco Legal do Saneamento',
		'ODS 6 & Dcada da gua da ONU',
		'Comits de Bacias Hidrogrficas',
		'gua Virtual e Pegada Hdrica',
		'Manejo Integrado da gua em Ambientes Urbanos',
	];

	const listMacroThemesRight = [
		'Governana do Oceano, Lei do Mar, reas Marinhas Protegidas, Polticas Pblicas',
		'Sustentabilidade do Oceano & ODS 14',
		'Dcada do Oceano da ONU & Cultura Ocenica',
		'Recursos Marinhos Ameaados',
		'Economia do Mar & Economia Azul',
		'Oceano como Marketing de Causa',
		'Carbono Azul, Blue Bonds',
	];

	return (
		<>
			<HeadPage image='/acquamater/head-talk.png' noMargin>
				<ContainerTextSingle maxWidth='lg'>
					<Typography variant='subtitle1' sx={{ color: '#fff' }}>
						Oferecemos uma srie de Palestras ligadas ao Mundo
						GUA-OCEANO que incentivam reflexes, pesquisas,
						mudanas de comportamento e tomadas de deciso. Janelas
						sobre o mundo para ver mais e alm. Uma "lente azul"
						atravs da qual conseguimos olhar para a vida, as
						relaes e os negcios sob um prisma disruptivo e
						revelador.
					</Typography>
				</ContainerTextSingle>
			</HeadPage>
			<ContainerTitle image='talk-wave.png'>
				<Title maxWidth='lg'>Palestras</Title>
			</ContainerTitle>
			<Wrapper>
				<ColumnTextImage
					image={{
						isBg: false,
						img: '/acquamater/patricia-palestra.png',
					}}
					title='Palestras Patricia'
				>
					A formao e a experincia de Patricia como Atriz e
					Professora fizeram dela uma verdadeira "Storyteller". Alm
					de apresentar e moderar eventos, d Palestras Inspiracionais
					para os mais variados pblicos, no Brasil e no exterior,
					compartilhando sua experincia pessoal, suas pesquisas e
					aprendizagens relacionadas ao Mundo GUA-OCEANO. Seu
					background artstico e sua pluralidade profissional permitem
					que misture temas, transmita dados cientficos e informaes
					atuais que favoream mudanas de comportamento e tomadas de
					deciso. Sua viso sistmica  capaz de sensibilizar as
					pessoas, visando  transformao humana, social e ambiental.
					<p>
						Lnguas Fluentes: portugus, ingls, espanhol, italiano.
					</p>
				</ColumnTextImage>

				<BoxGray waterMark>
					<TitleThemesBox variant='h2'>
						Voc est buscando uma Palestra impactante, inspiradora
						e criativa? Quer tocar o corao da plateia,
						convidando-a a pensar "fora da caixa"? Esses so alguns
						dos temas sobre os quais Patricia pode falar:
					</TitleThemesBox>
					<Grid container spacing={6}>
						<Grid item md={6}>
							<List>
								{listAllThemesLeft.map(item => (
									<li>{item}</li>
								))}
							</List>
						</Grid>
						<Grid item md={6}>
							<List>
								{listAllThemesRight.map(item => (
									<li>{item}</li>
								))}
							</List>
						</Grid>
					</Grid>
				</BoxGray>

				<ColumnTextImage
					image={{ isBg: false, img: '/acquamater/palestras-2.png' }}
					title='Palestras Parceiro'
					subtitle='(s atende no Brasil)'
					reverse
				>
					A Acqua Mater conta com um variado leque de Palestrantes
					(educadores, jornalistas, cientistas e empreendedores) que
					so especialistas em diversos tpicos ligados, precisamente,
					 GUA e ao OCEANO. Eles podem falar sobre os Macrotemas
					indicados abaixo em sua relao com a gua, com o Oceano ou
					com os dois  ou sobre temas mais especficos de cada um,
					como indicado nos boxes que seguem.
				</ColumnTextImage>

				<div style={{ width: '100%' }}>
					<BoxGray dense waterMark>
						<TitleThemesBlue variant='h2'>
							Macrotemas
						</TitleThemesBlue>
						<List>
							{listMacrothemes.map(item => (
								<li>{item}</li>
							))}
						</List>
					</BoxGray>
				</div>

				<ContainerThemes>
					<Grid container spacing='20px'>
						<Grid item md={6}>
							<BoxGray dense>
								<TitleThemesBlue variant='h2'>
									Macrotemas
								</TitleThemesBlue>
								<List>
									{listMacroThemesLeft.map(item => (
										<li>{item}</li>
									))}
								</List>
							</BoxGray>
						</Grid>

						<Grid item md={6}>
							<BoxGray dense waterMark>
								<TitleThemesBlue variant='h2'>
									Macrotemas
								</TitleThemesBlue>
								<List>
									{listMacroThemesRight.map(item => (
										<li>{item}</li>
									))}
								</List>
							</BoxGray>
						</Grid>
					</Grid>
				</ContainerThemes>
			</Wrapper>
			<Strip
				img='/acquamater/talk-ball.png'
				imgBG='/acquamater/talk-bg-bola.png'
				textBall='De que PALESTRA voc precisa?'
			/>
		</>
	);
};

export default Talk;
