import ColumnTextImage from '../../components/ColumnTextImage';
import {
	ContainerProducts,
	HeadPage,
	Description,
	ContainerProduct,
} from './styles';
import { Grid, Typography } from '@mui/material';
import { Wrapper } from '../styles';

const ProductsServices = () => {
	return (
		<>
			<HeadPage image={'/acquamater/head-products.png'}>
				<Grid container>
					<Grid item md={5}>
						<Typography
							variant='h1'
							sx={{ color: '#fff', fontSize: '2.5rem' }}
						>
							O que oferecemos
						</Typography>
					</Grid>
					<Grid item md={7} display="flex" justifyContent="end">
						<Description>
							<Typography variant='subtitle1' sx={{ color: '#fff' }}>
								Todos os Produtos da Acqua Mater so realizados
								tendo por eixo a GUA e o OCEANO e tm sinergia
								entre si, podendo se articular dependendo das
								necessidades do cliente
							</Typography>
						</Description>
					</Grid>
				</Grid>
			</HeadPage>

			<ContainerProducts>
				<ContainerProduct>
					<Wrapper>
						<ColumnTextImage
							image={{ isBg: false, img: '/acquamater/o-que-oferecemos-1.png' }}
							title='Palestras'
							pathName='/o-que-oferecemos/palestras'
							buttons={[
								{
									title: 'Saiba mais',
									url: '/palestras',
								},
							]}
						>
							Oferecemos uma srie de <strong>Palestras</strong>{' '}
							ligadas ao Mundo GUA-OCEANO que incentivam
							reflexes, pesquisas, mudanas de comportamneto e
							tomadas de deciso. Janelas sobre o mundo para ver
							mais e alm. Uma "lente azul" atravs da qual
							conseguimos olhar para a vida, relaes e os
							negcios sob um prisma disruptivo e revelador.
						</ColumnTextImage>
					</Wrapper>
				</ContainerProduct>
				<ContainerProduct>
					<Wrapper>
						<ColumnTextImage
							image={{ isBg: false, img: '/acquamater/o-que-oferecemos-2.png' }}
							title='Capacitao'
							pathName='/o-que-oferecemos/capacitacao'
							buttons={[
								{
									title: 'Saiba mais',
									url: '/capacitacao',
								},
							]}
							reverse
						>
							Oferecemos{' '}
							<strong>Workshops, Treinamentos e Cursos</strong>{' '}
							com o objetivo de sensibilizar, informar e formar
							multiplicadores do conhecimento que aprendam e
							apliquem novas ferramentas para atuar em prol da
							conservao, da regenerao e da sustentabilidade de
							todos os ecossistemas do Mundo GUA-OCEANO.
						</ColumnTextImage>
					</Wrapper>
				</ContainerProduct>

				<ContainerProduct>
					<Wrapper>
						<ColumnTextImage
							image={{ isBg: false, img: '/acquamater/o-que-oferecemos-3.png' }}
							title='Consultorias'
							pathName='/o-que-oferecemos/consultoria'
							buttons={[
								{
									title: 'Saiba mais',
									url: '/consultoria',
								},
							]}
						>
							Oferecemos Consultorias de{' '}
							<strong>Contedo de Curadoria</strong> e de{' '}
							<strong>Solues Integradas</strong> para ajudar
							pessoas e organizaes a adquirirem uma viso 360
							do Mundo GUA-OCEANO e para encontrar solues que
							gerem mais sade e mais vida em nosso planeta.
						</ColumnTextImage>
					</Wrapper>
				</ContainerProduct>

				<ContainerProduct>
					<Wrapper>
						<ColumnTextImage
							image={{ isBg: false, img: '/acquamater/last-products.png' }}
							title='Experincia'
							pathName='/o-que-oferecemos/experiencia'
							buttons={[
								{
									title: 'Saiba mais',
									url: '/experiencia',
								},
							]}
							reverse
						>
							Oferecemos Workshops, Viagens e Eventos Artsticos
							para reconectar as pessoas mais profundamente com o
							Mundo GUA-OCEANO sob as perspectivas pessoal,
							social e ambiental. Os participantes "mergulharo em
							suas prprias guas ao mesmo tempo em que iro
							desacelerar dos ritmos urbanos e ampliar sua
							percepo sobre a vida e o mundo.
						</ColumnTextImage>
					</Wrapper>
				</ContainerProduct>
			</ContainerProducts>
		</>
	);
};

export default ProductsServices;
