import { Grid, Typography } from '@mui/material';
import ColumnTextImage from '../../../components/ColumnTextImage';
import {
	ContainerTextSingle,
	ContainerTitle,
	HeadPage,
	Title,
} from '../styles';
import { List, Link, BlueBusinessHubContainer } from './styles';
import { Wrapper } from '../../styles';
import BoxGray from '../../../components/BoxGray';
import { colors } from '../../../theme/colors';
import { Strip } from '../../../components/Strip';

const Training = () => {
	const listThemes = [
		'Permacultura',
		'Agrofloresta',
		'Construo de Biodigestores',
		'Construo de banheiros secos para populaes vulnerveis',
		'Cosmtica Natural',
		'Saboaria Natural',
		'Detergentes Ecolgico',
	];

	return (
		<>
			<HeadPage image='/acquamater/head-training.png' noMargin>
				<ContainerTextSingle maxWidth='lg'>
					<Typography variant='subtitle1' sx={{ color: '#fff' }}>
						Oferecemos{' '}
						<strong>Workshops, Treinamentos e Cursos</strong> com o
						objetivo de sensibilizar, informar e formar
						multiplicadores do conhecimento que aprendam e apliquem
						novas ferramentas para atuar em prol da conservao, da
						regenerao e da sustentabilidade de todos os
						ecossistemas do Mundo GUA-OCEANO.
					</Typography>
				</ContainerTextSingle>
			</HeadPage>
			<ContainerTitle image='training-wave.png'>
				<Title maxWidth='lg'>Capacitao</Title>
			</ContainerTitle>
			<Wrapper>
				<ColumnTextImage
					image={{
						isBg: false,
						img: '/acquamater/training-first.png',
					}}
					title='ESCOLA NMADE DA GUA'
				>
					So projetos de Educao Ambiental que promovem Uma Nova
					Cultura da gua e a Cultura Ocenica. Idealizados e
					realizados pela Acqua Mater, so voltados, principalmente,
					para o pblico infanto-juvenil: comunidade escolar,
					festivais, eventos ao ar-livre etc.
					<br />
					<br />
					<Link>Conhea o Case do Projeto SOMOS GUA</Link>
				</ColumnTextImage>
			</Wrapper>
			<BlueBusinessHubContainer>
				<Wrapper>
					<Grid
						container
						justifyContent='space-between'
						alignItems='center'
					>
						<Grid item md={5}>
							<Typography variant='h1' color='white'>
								BLUE BUSINESS HUB
							</Typography>
							<br />
							<Typography variant='body3' color='white'>
								So Webinars e Treinamentos (presenciais e
								online) voltados, especificamente, ao pblico
								corporativo, para promover transformaes
								socioambientais tendo como ponto de partida o
								cuidado com o Mundo GUA-OCEANO.
								<br />
								<br />
								Neste momento, a Acqua Mater est desenvolvendo
								um treinamento de Cultura Ocenica para o Setor
								Privado dentro do Programa OLWA (Ocean Literacy
								With All) coordenado pela COI-UNESCO e endossado
								pela Dcada da ONU da Cincia Ocenica para o
								Desenvolvimento Sustentvel.
								<br />
								<br />
								<strong>Mais informaes em breve!</strong>
							</Typography>
						</Grid>
						<Grid
							item
							md={6}
							display='flex'
							alignItems='end'
							justifyContent='center'
						>
							<img
								src='/acquamater/training-second-image.png'
								alt='Training'
								width='90%'
							/>
						</Grid>
					</Grid>
				</Wrapper>
			</BlueBusinessHubContainer>

			<Wrapper style={{ marginTop: '50px' }}>
				<ColumnTextImage
					image={{
						isBg: false,
						img: '/acquamater/training-third.png',
					}}
					title='OCTOPUS'
				>
					So diversos Workshops, Treinamentos e Cursos (presenciais e
					online) que visam  sade das guas, principalmente para
					evitar a contaminao dos ecossistemas aquticos. So
					dirigidos a quem busca agir de modo mais sustentvel em suas
					propriedades, cidades ou empreendimentos comerciais
					(residncias, condomnios, comunidades, hotis, shoppings,
					centros comerciais etc).
					<br />
					<br />
					<Typography color={colors.primary}>ALGUNS DELES</Typography>
					<br />
					<List>
						{listThemes.map(item => (
							<li>{item}</li>
						))}
					</List>
				</ColumnTextImage>

				<div style={{ width: '100%' }}>
					<BoxGray dense waterMark>
						<ColumnTextImage
							image={{
								isBg: false,
								img: '/acquamater/training-last.png',
							}}
							noPadding
							textRight
							reverse
						>
							Essas capacitaes so realizadas pelos vrios
							Parceiros de confiana da Acqua Mater, que possuem
							diversas especializaes e encontram-se em
							diferentes Estados do Brasil.
							<br />
							Por isso, esse projeto chama-se OCTOPUS: eles so
							seus braos, ou tentculos, que alcanam para alm
							de seus prprios limites. Polvos tambm so animais
							marinhos extremamente inteligentes e conhecidos por
							sua imensa capacidade de resolver problemas.
						</ColumnTextImage>
					</BoxGray>
				</div>
			</Wrapper>
			<Strip
				img='/acquamater/training-ball.png'
				imgBG='/acquamater/training-bg-bola.png'
				textBall='De que CAPACITAO voc precisa?'
			/>
		</>
	);
};

export default Training;
